// spotify.js

import Cookies from 'js-cookie';

const AUTH_ENDPOINT = 'https://accounts.spotify.com/authorize';
const REDIRECT_URI = 'https://korsawat.github.io/Song/';
const CLIENT_ID = "e09c3389cd8f4a0c92f03e1bc5b423c2"; // Replace with your Spotify client ID
const SCOPES = ['user-library-read'];

export const storeAccessToken = (token) => {
  Cookies.set('spotify_access_token', token, { expires: 30 }); // Token expires in 30 days
};

export const getAccessToken = () => {
  return Cookies.get('spotify_access_token');
};

export const clearAccessToken = () => {
  Cookies.remove('spotify_access_token');
};

export const loginUrl = `${AUTH_ENDPOINT}?client_id=${CLIENT_ID}&redirect_uri=${REDIRECT_URI}&scope=${SCOPES.join(
  '%20'
)}&response_type=token&show_dialog=true`;

export const getTokenFromUrl = () => {
  const hash = window.location.hash.substring(1);
  const hashParams = new URLSearchParams(hash);
  const accessToken = hashParams.get('access_token');

  if (accessToken) {
    storeAccessToken(accessToken);
  }

  return { access_token: accessToken };
};

export const getLikedSongs = async (token) => {
  const limit = 50;
  let offset = 0;
  let allSongs = [];
  let data;

  try {
    do {
      const url = 'https://api.spotify.com/v1/me/tracks?limit=' + limit + '&offset=' + offset;
      const headers = {
        Authorization: 'Bearer ' + token,
      };

      const response = await fetch(url, { headers });

      if (!response.ok) {
        const errorText = await response.text();
        console.error('Response Status:', response.status);
        console.error('Response Text:', errorText);
        if (response.status === 403) {
          throw new Error('Access forbidden. Check Spotify API permissions.');
        } else {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
      }

      data = await response.json();
      allSongs = allSongs.concat(data.items);
      offset += limit;
    } while (offset < data.total);
  } catch (error) {
    console.error('Error fetching songs:', error.message);
    throw error;
  }

  return allSongs.map((song) => ({
    track: song.track,
    compared: false,
    wins: 0,
  }));
};
